*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}
html {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  line-height: 1.8;
}
body {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}